html {
  overflow-x: hidden;
}

body {
  background-color: $body-color;
  overflow: hidden;

  scroll-behavior: smooth;
}

::selection {
  background: lighten($color: $primary-color, $amount: 10);
  color: $white;
}

* {
  margin: 0;
  padding: 0;
}

#fullPage {
  overflow-x: hidden;
}

.fp-watermark {
  display: none;
}

footer a {
  margin: 0;
}

/* preloader */

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
}

ol,
ul {
  list-style-type: none;
  margin: 0px;
}

img {
  vertical-align: middle;
  border: 0;
}

a,
a:hover,
a:focus {
  text-decoration: none;
}

a,
button,
select {
  transition: 0.2s ease;

  &:focus {
    outline: 0;
  }
}

a:hover {
  color: $primary-color;
}

.slick-slide {
  outline: 0;
}

.section {
  padding-top: 100px;

  &-title {
    margin-bottom: 30px;
  }

  & > * {
    height: 100%;
  }
}

.bg-cover {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

div.fp-overflow {
  display: flex;
  flex-direction: row;
  align-items: center;
}


/* overlay */

.overlay {
  position: relative;

  &::before {
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background: $black;
    opacity: 0.5;
  }
}

.outline-0 {
  outline: 0 !important;
}

.d-unset {
  display: unset !important;
}

.page-title {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.feature-item:is(:hover, :focus) .feature-icon {
  transition: 350ms;
  transform: scale(1.125);
}

.feature-item h4 {
  position: relative;
  width: max-content;
}

.feature-item h4::after {
  content: "";
  position: absolute;

  top: 117%;
  left: 0;
  width: 100%;

  background-color: #000;
  height: 2px;
  transform: scaleX(0);
  transform-origin: left;
  transition: 350ms;
}

.feature-item:is(:hover, :focus) h4::after {
  transform: scaleX(1);
}

.bg-primary {
  background: $primary-color !important;
}

.bg-gray {
  background: $gray;
}

.text-primary {
  color: $primary-color !important;
}

.text-dark {
